<template>
  <div>
    <nav-bar :name="title"></nav-bar>
<v-card class="ma-1" cols="12">
<v-container>
  <v-row>
    <v-col cols="6">
      <v-text-field
      class="mb-n5"
      outlined
      dense
      v-model="user.firstname"
      label="*First name"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
      class="mb-n5"
      outlined
      dense
      v-model="user.lastname"
      label="*Last name"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="8">
      <v-file-input
      class="mb-n5"
      label="*Choose file"
      outlined
      dense
      v-model="user.img"
      ></v-file-input>
    </v-col>
    <v-col cols="4">
      <v-checkbox
    class="mb-n5 mt-0"
    outlined
    dense
      v-model="user.staff"
      label="Is staff"
      required
      ></v-checkbox>
    </v-col>
    <v-col cols="12">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.username"
      label="*Username"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.phone"
      label="Phone number"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.email"
      label="*Email address"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.password"
      label="*Password"
      required
      type="password"
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.confirm_password"
      label="*Confirm password"
      required
      type="password"
      ></v-text-field>
    </v-col>
    <v-col>
    <v-btn
      class="mx-4 my-2"
      color="#989898"
      dark
            @click="$router.go(-1)"
    >
      GO BACK
    </v-btn>
    <v-btn
      class="mx-4 my-2"
      color="#E47911"
      dark
      :loading="loading"
      @click="add_user()"
    >
      ADD USER
    </v-btn>
    </v-col>
  </v-row>
</v-container>
</v-card>
  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import UserService from '@/services/UserService'
  import Swal from 'sweetalert2'

  export default {
    components: {
      NavBar
    },
    data: () => ({
      title: 'ADD USER',
      loading: false,
      user:{
        firstname: '',
        lastname: '',
        img: null,
        username: '',
        phone: '',
        email: '',
        staff: false,
        password: '',
        confirm_password:''
      }
    }),

    methods:{
      add_user(){
        this.loading = true
        UserService
          .register(this.user)
          .then(() => {
            Swal.fire({
                  icon: 'success',
                  title: 'Success',
                  text: 'The user successfully added.'
              }).then(() => {
                  this.loading = false
                  this.$router.push('/users')
              })
          })
          .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
                console.log(e.response.data)
                Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: e.response.data
                });
    
            })
      }
    }
  }
</script>